body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: red;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  max-width: 75%;
  height: auto;
}

.card {
  max-width: 345px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.card:hover {
  cursor: pointer;
}

.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-item-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.nav-item {
  padding: 1rem;
}

.nav-item:hover {
  cursor: pointer;
}

.loading {
  text-align: center;
}

.movie-detail-container {
  margin: 0 auto;
  width: 50rem;
  text-align: center;
}
.movie-detail-body {
  text-align: center;
}
